// next-app/pages/register.js

import { useState } from 'react';
import axios from '../services/api';
import { useRouter } from 'next/router';
import { useAuth } from '../context/AuthContext'; // Make sure you have this import if you're using AuthContext
import Link from 'next/link'; // Import Link for the login link

export default function Register() {
  const [username, setUsername] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const router = useRouter();
  const { login } = useAuth(); // If you have a login function in AuthContext, use it

  const handleSubmit = async (e) => { // Renamed from handleRegister
    e.preventDefault(); // Prevent default form submission behavior

    try {
      const res = await axios.post('/auth/register', { username, email, password });
      console.log('Registration Success:', res.data);

      // These lines assume your backend's /auth/register endpoint
      // returns both `token` and a `user` object (e.g., res.data.user = {_id, username, email})
      localStorage.setItem('token', res.data.token);
      localStorage.setItem('user', JSON.stringify(res.data.user)); // Store the user object

      // If you want to auto-login after register, use AuthContext's login:
      if (login) login(res.data.token, res.data.user);

      alert('Registration successful!');
      router.push('/'); // Redirect to homepage after registration
    } catch (err) {
      console.error('Registration Failed:', err.response?.data || err.message);
      alert(err.response?.data?.error || 'Registration failed.'); // Provide user feedback
    }
  };

  return (
    // Outer div with the specified styling
    <div className="p-4 max-w-md mx-auto">
      {/* Form wrapping the inputs and button */}
      <form onSubmit={handleSubmit}>
        {/* H2 with the specified styling */}
        <h2 className="text-xl font-bold">Register</h2>
        
        {/* Input fields with the specified styling */}
        <input 
          className="border p-2 w-full mt-4" // Added mt-4 for spacing after h2
          value={username} 
          onChange={e => setUsername(e.target.value)} 
          placeholder="Username" 
          required // Added required attribute
        />
        <input 
          className="border p-2 w-full mt-2" 
          value={email} 
          onChange={e => setEmail(e.target.value)} 
          placeholder="Email" 
          type="email" // Ensure type is email
          required // Added required attribute
        />
        <input 
          className="border p-2 w-full mt-2" 
          type="password" 
          value={password} 
          onChange={e => setPassword(e.target.value)} 
          placeholder="Password" 
          required // Added required attribute
        />
        
        {/* Button with the specified styling */}
        <button 
          type="submit" // Type submit for form submission
          className="bg-green-600 text-white p-2 mt-4 w-full">
            Register
        </button>
      </form>

      {/* Link for existing users */}
      <p className="mt-4 text-center text-sm text-gray-600">
        Already have an account?{' '}
        <Link href="/login" className="text-blue-500 hover:text-blue-800">
          Login
        </Link>
      </p>
    </div>
  );
}